.carousel-gallery-default {
    padding: 1rem;
    background-color: #3e1031;
}

.carousel-gallery {
    overflow: hidden;
    position: relative;

    .swiper-slide {
        margin-right: 0;
        margin-top: 0;
    }

    .swiper-slide__text {
        display: block;
        top: 0;
        background-color: initial;
        width: 100%;
        margin-right: unset;
        margin-left: unset;
        padding: 1rem 0;
    }

    .swiper-buttons-wrap {
        bottom: 0.5rem;
        right: 0;
    }
}

.galleria-thumbnails-wrap {
    width: calc(100% - 7rem);
    overflow: hidden;
}

.galleria-thumbnails-list {
    --thumbnails: 1;
    display: flex;
    list-style: none;
    margin: 0;
    width: calc(var(--thumbnails) * 25%);
    max-width: none;
    align-items: baseline;
    transition: transform 0.5s;

    .galleria-thumbnail {
        width: calc(100% / var(--thumbnails));
        margin-top: 0;
        margin-right: 0.5rem;
        &.active img {
            border: 1px solid red;
        }
    }
}
